@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@500&family=Hanalei&family=Lobster&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend+Mega&family=Oswald:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  width: 100%;
  height: 100vh;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.MainPage_container__brLBH {
  width: 60%;
  height: 80vh;
  border: 20px dashed aliceblue;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: purple;
}

.MainPage_container__brLBH h1 {
  font-size: 45px;
  font-family: 'Hanalei', cursive;
  letter-spacing: .5rem;
  text-transform: uppercase;
  color: aliceblue;
}
.MainPage_category__2i3yt , 
.MainPage_level__2ryM3 {
  width: 500px;
  display: flex;
}
.MainPage_category__2i3yt label , 
.MainPage_level__2ryM3 label {
  font-size: 20px;
  font-family: 'Lobster', cursive;
  letter-spacing: .2rem;
  color: aliceblue;
  margin-right: 50px;
}
.MainPage_category__2i3yt select ,
.MainPage_level__2ryM3 select {
  width: 200px;
  height: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 5px solid aliceblue;
  color: aliceblue;
  font-family: 'Cinzel', serif;
  cursor: pointer;
}
.MainPage_category__2i3yt select option,
.MainPage_level__2ryM3 select option {
  background-color: black;
}
.MainPage_level__2ryM3 select:hover,
.MainPage_category__2i3yt select:hover {
  background-color: black;
}

.MainPage_button__3xOQ4 {
  font-size: 15px;
  padding: 15px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  border: 5px dashed white;
  color: aliceblue;
  font-family: 'Pacifico', cursive;
  letter-spacing: .2rem;
}
.MainPage_button__3xOQ4:active {
  border: 5px dashed white;
}
.MainPage_button__3xOQ4:hover {
  background-color: rgb(17, 5, 126);
}
@media all and (max-width:950px) {
  
  .MainPage_category__2i3yt , 
  .MainPage_level__2ryM3 {
    flex-direction: column;
    text-align: center;
  }
  .MainPage_category__2i3yt label , 
  .MainPage_level__2ryM3 label {
    width: 200px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .MainPage_category__2i3yt select , 
  .MainPage_level__2ryM3 select {
    margin: 0 auto;
  }
  .MainPage_button__3xOQ4 {
    font-size: 10px;
    padding: 10px;
  }
}
@media all and (max-width:650px) {
  .MainPage_container__brLBH {
    width: 100%;
    height: 100vh;
  }
}
@media all and (max-width:500px) {
  .MainPage_category__2i3yt , 
  .MainPage_level__2ryM3 {
    width: 200px;
  }
  .MainPage_category__2i3yt select option,
.MainPage_level__2ryM3 select option {
  font-size: 8px;
}
}
